<template>
<nav>
  <a href="/addImage"  class="add">Add Image</a>
</nav>
  <form class="search_form" @submit.prevent="FindByCategories()">
    <input type="text" placeholder="search by categories" v-model="categories">
    <input type="submit" value="search">
  </form>

  <h2>Select image on the map:</h2>
  <div id="mapWrapper">
    <div id="map"></div>
    <div v-if="image">
      <img v-bind:src="'data:image/jpeg;base64,'+image.url" alt="image" class="image"/>
      <p>date:</p>
      <p class="value_paragraph">{{image.date}}</p>
      <p>latitude:</p>
      <p class="value_paragraph">{{image.lat}}</p>
      <p>longitude:</p>
      <p class="value_paragraph">{{image.lng}}</p>
      <p>categories:</p>
      <li v-for="item in image.categories" class="value_paragraph">
        {{ item }}
      </li>
    </div>
  </div>

</template>

<script>
import axios from 'axios'
import YandexMap from '../mapScript'

export default {

  data() {
    return {
      error: false,
      image: '',
      images: [],
      categories: [],
      objectManager: Object
    }
  },
  mounted() {
    this.$nextTick(function () {
      axios.get('/api/image?page='+0).then(response => {
        this.images=response.data.data
      }).catch((error) => {
        console.log(error.response.status)
      })


      this.CreateMap()



    })
  },
  methods: {
    setImage(e){
      var objectId = e.get('objectId');
        this.image=this.images.find(value => value.id===objectId)
    },
    FindByCategories(){
      const myNode = document.getElementById("mapWrapper");
      myNode.innerHTML = '';
      this.images=[]
      axios.get('/api/image/categories?categories='+this.categories).then(response => {
        this.images=response.data.data
      }).catch((error) => {
        console.log(error.response.status)
      })
      console.log(this.images)
      this.CreateMap()

    },
    CreateMap(){
      ymaps.ready(() => {
        const myMap = new ymaps.Map('map', {
          center: [53.893009, 27.567444],
          zoom: 10
        });

        const objectManager = new ymaps.ObjectManager({
          clusterize: true,
        });
        objectManager.objects.options.set('preset', 'islands#greenDotIcon');
        objectManager.clusters.options.set('preset', 'islands#greenClusterIcons');
        myMap.geoObjects.add(objectManager);
        for (let i = 0; i <this.images.length ; i++) {
          const image = JSON.stringify({type: 'Feature', id: this.images[i].id, geometry: {type: 'Point',
              coordinates: [this.images[i].lat,this.images[i].lng]} })
          objectManager.add(image)
        }

        objectManager.objects.events.add(['mouseenter'], this.setImage);

      })

    }
  }
}
</script>

<style>
#map {
  width: 600px;
  height: 400px;
  margin-left: 35%;
}

p {
  font-size: 23px;
}
li{
  font-size: 17px;
}
.add {
  color: blue;
  text-decoration: none;
}
.image{
  max-height: 400px;
  max-width: 600px;
  margin-top:20px
}
.value_paragraph{
  font-size: 17px;
  font-style: italic;
}
.search_form{
  display:flex;
  margin-left: 45%;
}
</style>
